const user = {
	namespaced: true,
	state: {
		userInfo: null
	},
	mutations: {
		updateUserInfo(state, data) {
			let user = {
        userId: data.userId,
				userImg: data.userImg,
				userName: data.userName
			}
			state.userInfo = user;
			localStorage.setItem('userInfo', JSON.stringify(user))
		}
	},
	getters: {
		getUserInfo: (state, getters, rootState) => {
			if (state.userInfo === null) {
				state.userInfo = JSON.parse(localStorage.getItem('userInfo'))
				if (rootState.getURL != undefined && rootState.getURL != null && rootState.getURL != '') {
					state.userInfo.userImg = rootState.getURL + state.userInfo.userImg;
				}
			}
			return state.userInfo
		},
	}
}

export default user
